.logo-img {
    width: 200px;
    height: auto;
    margin: 0 20px;
}

nav {
    display: flex;
    justify-content: space-between;
    padding: 10px 32px;
    background-color: rgb(232, 232, 232);
    border-bottom: solid 4px #65d96a;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 30px;
    padding: 0;
    margin: 0;
    list-style: none;
}

.link {
    text-decoration: none;
    color: black;
    font-weight: 500;
    font-size: 24px;
    transition: all 300ms ease;
}

.link:hover {
    color: #71db77;
}

nav button {
    background-color: #71db77;
    color: rgb(0, 0, 0);
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 20px;
    cursor: not-allowed;
}

nav button:hover {
    background-color: #5ac162;
}


@media (max-width: 768px) {
    .logo-img {
        width: 150px;
        margin: 0 10px;
    }

    nav {
        padding: 10px 16px;
    }

    .nav-links {
        gap: 20px;
    }

    .link {
        font-size: 18px;
    }

    nav button {
        display: none;
    }
}

@media (max-width: 480px) {
    .logo-img {
        width: 120px;
        margin: 0 5px;
    }

    .nav-links {
        gap: 10px;
    }

    .link {
        font-size: 16px;
    }
}