footer {
    background-color: #171717;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
    border-top: solid 2px #71db77;
}

ul {
    list-style: none;
}

.footer-links {
    display: flex;
    align-items: center;
    gap: 30px;
    padding: 0;
    margin: 0;
    list-style: none;
}

.footer-link {
    text-decoration: none;
    color: white;
    font-size: 24px;
    font-weight: 300;
    transition: all 300ms ease;
}

.contact:hover {
    cursor: not-allowed;
}

.footer-link:hover {
    color: #71db77;
}

@media (max-width: 768px) {
    .footer-links {
        gap: 20px;
        flex-wrap: wrap;
        justify-content: center;
    }

    .footer-link {
        font-size: 18px;
    }
    .footer-copyright {
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    footer {
        padding: 10px 0;
    }
}